* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  html {
    font-size: calc(13px + 0.390625vw);

}

.app-light {
    margin: auto;
    min-height: 100vh;
    background-color: white;
}

.app-dark {
  margin: auto;
  min-height: 100vh;
  background-color: black;
  
}

@media screen and (min-width: 640px) {
  .navbar, .search{
    width:80%;
    margin: auto;
  }
  #dictionaryImg {
    width: 50%;
  }

}
@media screen and (min-width: 920px) {
  .navbar, .search{
    width:70%;
  }
  #dictionaryImg {
    width: 50%;
  }
}

@media screen and (min-width: 1024px) {
  .navbar, .search{
    width:65%;
  }
  html {
    font-size: calc(11px + 0.390625vw);
  }
  #dictionaryImg {
    width: 60%;
  }
}

@media screen and (min-width: 1280px) {
  .navbar, .search{
    width:50%;
  }
  #dictionaryImg {
    width: 70%;
  }
}

@media screen and (min-width: 1640px) {
  .navbar, .search, .main {
    width:45%;
  }
  #dictionaryImg {
    width: 60%;
  }
}