    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3%;
}

.book-icon-light {
  color: #757575;
}
.book-icon-dark {
    color: #A644ED;
}
/* Toggle switch ___________*/

.toggle {
  margin-top: 8%;
  position: relative;
  display: inline-block;
  width: 2.7rem;
  height: 1.4rem;
  background-color: #757575;
  border-radius: 35px;
  border: none;
  cursor: pointer;
}

/* After slide changes */
.toggle:after {
  content: "";
  position: absolute;
  width: 1.1rem;
  height: 1.1rem;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  top: .15rem;
  left: 0.3rem;
  transition: all 0.5s;
}

/* Toggle text */
p {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

/* Checkbox checked effect */
.checkbox:checked + .toggle::after {
  left: 1.4rem;
}

/* Checkbox checked toggle label bg color */
.checkbox:checked + .toggle {
  background-color:  #A644ED;
  ;
}

/* Checkbox vanished */
.checkbox {
  display: none;
}


.dark-mode {
