* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.search {
    display: flex;
    justify-content: center;
}
form {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    float: left;
    width: 85%;
}
.search-light {
    width: 80%;
    height: 3rem;
    border-radius: 15px 0 0 15px;
    border:none;
    padding: 0 7%;
    font-weight: 700;
    font-family: 'Domine', serif;
    font-size: 0.9rem;
    background-color: #F4F4F4;
}
.search-light:focus{
    outline: none;
}
.search-dark {
    width: 80%;
    height: 3rem;
    border-radius: 15px 0 0 15px;
    border:none;
    padding: 0 7%;
    font-weight: 700;
    font-family: 'Domine', serif;
    font-size: 1rem;
    color: white;
    background-color: #1F1F1F;
}

.search-dark::placeholder {
    color: rgb(202, 201, 201)
}
.search-dark:focus{
    outline: none;
}
.search-btn-light {
  float: left;
  width: 20%;
  cursor: pointer;
  border: none;
  border-radius: 0 15px 15px 0;
  background-color: #F4F4F4;

}
.search-btn-dark {
    float: left;
    width: 20%;
    cursor: pointer;
    border: none;
    border-radius: 0 15px 15px 0;
    background-color: #1F1F1F;
}
.search-icon {
    color: #A644ED;;
}
