* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

html {
  font-size: calc(15px + 0.390625vw);
  font-family: "Heebo", sans-serif;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#dictionaryImg {
  max-width: 70%;
}
#dictionaryImg img {
  width: 100%;
}

.wordName {
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 6% 0;
}

.word {
  width: 50%;
}

.font-dark {
  color: black;
}

.font-light {
  color: white;
}

.audio {
  width: 20%;
  display: flex;
  justify-content: flex-end;
}

#wordAudio {
  width: 70%;
  cursor: pointer;
}

#wordAudio img {
  width: 100%;
}

.word h1 {
  font-size: 2.5rem;
}

.wordName p {
  font-size: 1rem;
  margin-top: 8%;
  color: #a644ed;
}

.types {
  margin-bottom: 3%;
  width: 85%;
  display: flex;
  flex-direction: column;
}

.type {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.type p {
  font-family: "Domine", serif;
  font-size: 1.1rem;
}
.type span {
  height: 30%;
  width: 85%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid rgba(145, 142, 142, 0.425);
}
.meaning {
  margin-top: 6%;
}

.meaning h3,
.synonyms h3 {
  color: rgb(134, 133, 133);
  font-family: "Heebo", sans-serif;
  font-weight: 400;
  font-size: 0.9rem;
}

.meaning ul {
  margin: 5% 0 0 8%;
}

.meaning li {
  margin-top: 3%;
  font-family: "Heebo", sans-serif;
  font-size: 0.8rem;
}

.synonyms {
  display: flex;
  gap: 2rem;
  align-items: baseline;
}

.synonyms p {
  margin: 0 0 2% 1%;
  color: #a644ed;
  font-family: "Heebo", sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

.synonyms_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.hidden {
  display: none;
}

.definition {
  font-weight: 400;
  margin-top: 5%;
}

.example {
  font-weight: 400;
  color: rgb(134, 133, 133);
  font-style: italic;
  margin-top: 3%;
}

@media screen and (min-width: 640px) {
   .search-light, .search-dark {
    padding: 0 3%;
   }
   .search-btn-light, .search-btn-dark {
    width: 10%;
   }
   form{
    width: 100%;
   }
   main{
    width: 80%;
    margin: auto;
  }
  }
  @media screen and (min-width: 920px) {
    #wordAudio img {
      width: 80%;
      
    }
    #wordAudio{
      display: flex;
      justify-content: right;
    }
    main{
      width: 70%;
      margin: auto;
    }
  }
@media screen and (min-width: 1024px) {

  main{
    width: 65%;
    margin: auto;
  }
}

@media screen and (min-width: 1280px) {
 
  main{
    width: 48%;
    margin: auto;
  }
}
